import React,{useContext,useState,useEffect} from 'react'
import {useParams,Link} from 'react-router-dom'
import ProductItem from '../utils/productItem/ProductItem';
import { GlobalState } from './../../../GlobalState';

function DeatailProduct() {
    const params = useParams()
    const state = useContext(GlobalState)
    const [products] = state.productsAPI.products
    const addcart = state.productsAPI.addcart
    const [detailProduct, setDetailProduct] = useState([])

    useEffect(() => {
        if(params.id) {
            products.forEach(product => {
                if(product._id === params.id) 
                    setDetailProduct(product)
            });
        }
    },[params.id,products])

    if (detailProduct.length === 0)
        return null;
  return (
    <>
        <div className='detail'>
            <img src={detailProduct.images.url} alt=''/>
            <div className='box-detail'>
                <div className='rwo'>
                    <h2>{detailProduct.title}</h2>
                    <h6>#id: {detailProduct.product_id}</h6>
                </div>
                <span>${detailProduct.price}</span>
                <p>{detailProduct.description}</p>
                <p>{detailProduct.content}</p>
                <p>Sold: {detailProduct.sold}</p>
                <Link to="/cart" className='cart' onClick={() => addcart(detailProduct)}>Buy now</Link>
            </div>
        </div>

        <div>
            <h2> Related products</h2>
            <div className='products'>
                {
                    products.map(product => {
                        return product.category === detailProduct.category ? <ProductItem key={product._id} product={product} />: null
                    })
                }
            </div>
        </div>

    </>
  )
}

export default DeatailProduct
